<template>
  <div class="content">
    <!-- title -->
    <van-nav-bar
      title="申请退款"
      right-text="支持银行"
      v-if="pos"
      left-arrow
      @click-left="onClickLeft"
      @click-right="onClickRight"
    >
      <slot name="left" slot="left"><i class="icon-back"></i></slot>
    </van-nav-bar>
    <van-nav-bar
      title="申请退款"
      v-if="!(pos)"
      left-arrow
      @click-left="onClickLeft"
    >
      <slot name="left" slot="left"><i class="icon-back"></i></slot>
    </van-nav-bar>
    <!-- <div class="tip">
      <span>退款完成后系统将自动扣减佣金</span>
    </div> -->
      <div class="own-not-cash" v-if="bankCardSelf">
        <div class="tip">
          <span style="color: #E58B49;font-size: 0.24rem;">请认真核对您的收款账号信息，提交后无法修改！</span>
        </div>
      </div>
      <div class="other-cash" v-if="pos">
        <div class="tip-other">
          <div class="tip-content">
            <p style="color: #E58B49;">请认真核对您的收款账号信息，提交后无法修改！</p>
            <p style="color: #E58B49;">您所填写的收款账号信息，我们将严格保密，确保不会被泄露。</p>
          </div>
        </div>
      </div>
    <div class="refund-type left-12 has-radio" style="margin-top: 0.24rem">
      <van-cell is-link  @click.stop="showPopupType">
         <div class="uni-list-cell-left">
            <!-- <div class="must">
              <span style="display:inline-block;height:0.14rem;width:0.14rem;font-size:0.24rem;line-height:0.24rem;">*</span>
            </div> -->
              退款类型
          </div>
          <div class="uni-list-cell-db" >
            <div class="uni-input wait-select" v-if="ifChange">请选择</div>
            <div class="uni-input" v-if="!ifChange">{{array[index]}}</div>
          </div>

      </van-cell>
        <!-- 选择器 -->
        <van-popup v-model="showType" position="bottom" :close-on-click-overlay="false" :style="{ height: '7.08rem' }">
          <!-- <van-picker show-toolbar title="退款类型" :columns="array" @cancel="onCancelType" @confirm="onConfirmType" @change="bindPickerChange"></van-picker> -->
          <div class="pop-header">
            退款类型
            <div class="header-close" @click="onConfirmType(array[index],index)">
              <img  src="data:image/png;base64,"/>
           </div>
          </div>
          <div class="pop-container">
          <van-radio-group v-model="index">
            <van-cell-group>
              <van-cell v-for="(item,i) in array" :key="i+1" :name="i" :title="item" clickable @click="index = i;ifChange=false">
                <template #right-icon>
                  <van-radio icon-size="16px" :name="i" />
                </template>
              </van-cell>
              <van-cell style="height:1.5rem"></van-cell>
            </van-cell-group>
          </van-radio-group>
          </div>
          <div class="pop-bottom">
            <div class="pop-bottom-btn" @click="onConfirmType(array[index],index)">确定</div>
          </div>
        </van-popup>
    </div>
    <!-- 退款原因 -->
    <div class="refund-type refund-why left-12 has-radio">
      <van-cell is-link @click="showPopupWhy">
         <div class="uni-list-cell-left">
            <!-- <div class="must">
              <span style="display:inline-block;height:0.14rem;width:0.14rem;font-size:0.24rem;line-height:0.24rem;">*</span>
            </div> -->
              退款原因
          </div>
          <div class="uni-list-cell-db" >
            <div class="uni-input wait-select" v-if="ifWhy">请选择</div>
            <div class="uni-input" v-if="!ifWhy">{{reason?reason[indexWhy]:''}}</div>
            <!-- <div class="uni-input" v-if="!ifWhy">{{reason}}</div> -->

          </div>
      </van-cell>
         <!-- 选择器 -->
        <van-popup v-model="showWhy" position="bottom" :close-on-click-overlay="false" :style="{ height: '7.08rem' }">
          <!-- <van-picker show-toolbar title="退款原因" :columns="reason" @cancel="onCancelWhy" @confirm="onConfirmWhy" @change="bindPickerChangeWhy"/> -->
              <div class="pop-header">
                  退款原因
                  <div class="header-close" @click="onConfirmWhy(indexWhy)">
                    <img  src="data:image/png;base64,"/>
                  </div>
              </div>
              <div class="pop-container">
              <van-radio-group v-model="indexWhy">
                <van-cell-group>
                  <van-cell v-for="(item,i) in reason" :key="i+1" :name="i" :title="item" clickable @click="indexWhy = i;ifWhy=false">
                    <template #right-icon>
                      <van-radio icon-size="16px" :name="i" />
                    </template>
                  </van-cell>
                  <van-cell style="height:1.5rem"></van-cell>
                </van-cell-group>
              </van-radio-group>
              </div>
              <div class="pop-bottom">
                <div class="pop-bottom-btn" @click="onConfirmWhy(indexWhy)">确定</div>
              </div>
        </van-popup>
    </div>
    <!-- 退款金额 -->
    <div class="refund-type left-12 no-border">
      <div class="fefund-num">
        <div class="uni-list-cell-left">
          <!-- <div class="must">
            <span style="display:inline-block;height:0.14rem;width:0.14rem;font-size:0.24rem;">*</span>
          </div> -->
            退款金额
          <div class="fefund-money">￥{{toFixedTwo(data.orderAmount)}}</div>
        </div>
      </div>
    </div>
    <div class="fefund-other" v-if="otherFefund">
      <div>其它：<span v-if="otherFefund.usedDiamondCount">钻石{{otherFefund.usedDiamondCount}}个<span v-if="otherFefund.usedSeashellCount">，</span></span> <span v-if="otherFefund.usedSeashellCount">积分{{otherFefund.usedSeashellCount}}个</span><span v-if="otherFefund.smartEcardAmount"> 智家E卡{{otherFefund.smartEcardAmount}}元</span><span v-if="otherFefund.usedSeashellCount || otherFefund.usedDiamondCount || otherFefund.smartEcardAmount">。 </span></div>
    </div>

    <!-- 本人刷卡非现金 -->
    <div class="own-not-cash own-not-cash-top-margin" v-if="bankCardSelf">
      <!-- <div class="tip">
        <span style="color: #FFB22B;font-size: 0.24rem;">请认真核对您的收款账号信息，提交后无法修改！</span>
      </div> -->
      <div class="bank-model">
        <van-cell-group>
          <van-field
            :value="bankCardSelf"
            label="银行卡号"
            disabled
          />
        </van-cell-group>
      </div>
    </div>
    <!-- 他人代刷现金 -->
    <div class="other-cash cash-input" v-if="pos">
      <!-- <div class="tip-other">
        <div class="tip-content">
          <p style="color: #FFB22B;">请认真核对您的收款账号信息，提交后无法修改！</p>
          <p style="color: #FFB22B;">您所填写的收款账号信息，我们将严格保密，确保不会被泄露。</p>
        </div>
      </div> -->
      <div class="refund-type no-top">
        <van-cell-group>
          <van-field
            v-model="userName"
            label="姓名"
            placeholder="请输入姓名"
            @blur="checkName"
          />
          <van-field
            v-model="bankCard"
            label="银行卡号"
            placeholder="请输入借记卡号，不支持信用卡"
            @blur="checkCard"
          />
          <van-field
            v-model="userId"
            label="身份证号"
            placeholder="请输入身份证号"
            @blur="checkId"
          />
          <van-field
            v-model="userPhone"
            label="预留手机号"
            placeholder="请输入银行预留手机号"
            @blur="checkPhone"
          />
        </van-cell-group>
      </div>
    </div>
    <!-- 退款说明 -->
    <div class="refund-des">
      <div class="des-title">
        退款说明
      </div>
      <div class="des-area">
        <van-cell-group>
          <van-field
            type="textarea"
            v-model="description"
            maxlength="500"
            show-word-limit
          />
        </van-cell-group>
      </div>
    </div>
    <div class="refund-bottom"  v-if="data.orderRepairType === 2">
      <div class="refund-png">
        <div class="png-title">上传凭证</div>
        <upload-img
          style="margin-top: 12px"
          :multiple="true"
          :maxCount="6"
          v-model="images"
          cameraDes="最多6张"
        ></upload-img>
        <!-- <div class="photo-show">
            <van-uploader
              v-model="images"
              multiple
              :after-read="uploadImg"
              :max-count="6"
              v-if="data.orderRepairType === 2"
            >
              <img class="camera" :src="`${$cdnImageUrl}/pc/camera.png`"/>
              <div class="camera-des">最多6张</div>
            </van-uploader>
            <van-loading type="spinner" color="#1989fa" v-if="loading"/>
        </div> -->
      </div>

    </div>
    <div class="up">
      <div class="up-btn" @click="submission">提交</div>
    </div>
    <invoice-tip-modal v-if="invoiceInvaild"/>
  </div>
</template>
<script>
import personalApi from '@/http/service/index';
import Vue from 'vue';
import { Toast } from 'vant';
// import InvoiceTipModal from './InvoiceTipModal';
const InvoiceTipModal = () => import('./InvoiceTipModal');
const UploadImg = () => import('@/components/uploadImg');
Vue.use(Toast);
export default {
  components: {
    InvoiceTipModal,
    UploadImg
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      if (from.name === 'orderDetail') {
        vm.firstReturn = true
      }
    })
  },
  mounted() {
    // 申请退货退款页面信息
    const data = {
      cOrderSn: null, // 网单号
    }
    // 修改申请传参
    if (this.$route.query.repairSn) {
      this.repairSn = this.$util.strJSONParse(this.$route.query.repairSn, '')// 逆向单号+网单号+ 网单id
      data.cOrderSn = this.repairSn.cOrderSn
    } else {
      this.repairSnOne = this.$route.query.cOrderSn
      this.orderProductId	 = this.$route.query.orderProductId
      console.log('orderProductId', this.orderProductId)
      data.cOrderSn = this.repairSnOne
    }
    personalApi.orderRepair(data).then(res => {
      this.data = res.data.data
      this.reasonAll = res.data.data.reason

      if (res.data.data.usedDiamondCount || res.data.data.usedSeashellCount || res.data.data.smartEcardAmount) {
        this.otherFefund = {
          usedDiamondCount: res.data.data.usedDiamondCount,
          usedSeashellCount: res.data.data.usedSeashellCount,
          smartEcardAmount: res.data.data.smartEcardAmount
        }
      }

      // 退款类型处理
      res.data.data.typeActual.forEach(item => {
        this.array.push(item.typeActualStr)
      })
      // 银行卡号
      this.bankCardSelf = res.data.data.bank
      this.pos = res.data.data.pos
    })
    personalApi.refundVerifyInvoice({ netSn: data.cOrderSn }).then(res => {
      if (res.data.success && !res.data.data) {
        this.invoiceInvaild = true;
      }
    });
  },
  watch: {
    typeName(newVal) {
      this.indexWhy = null
      if (newVal === '仅退款') {
        this.reasonIndex = 3
      } else if (newVal === '退货不退款') {
        this.reasonIndex = 2
      } else {
        this.reasonIndex = 1
      }
      this.reason = this.reasonAll[this.reasonIndex]
    }
  },
  data() {
    return {
      orderProductId: null,
      firstReturn: false, // 首次退货路由控制
      reasonIndex: null, // 退款类型传参（1退款退货/3仅退款）
      loading: false,
      pos: false, // 本人（false）他人（true）
      userPhone: '', // 手机号
      userId: '', // 身份证号
      userName: '', // 姓名
      bankCard: null, // 银行卡号
      bankCardSelf: null, // 自付银行卡
      showType: false, // 弹窗退款类型
      showWhy: false, // 弹窗退款原因
      title: 'picker',
      ifChange: true,
      ifWhy: true,
      activeIcon: `${this.$cdnImageUrl}/cart/icon-selected.png`,
      inactiveIcon: `${this.$cdnImageUrl}/cart/icon-unselected.png`,
      data: {
        orderRepairType: 2, // 1：大家电；2：社会化/小家电（当前业务区别，大家电申请逆向不能上传图片，前端需隐藏上传图片入口）
        reason: {
          3: [
            '不想要',
            '空包裹',
            '快递/物流一直未送达',
            '快递无跟踪记录',
            '破损已拒签'
          ]
        }, // 可选的退款原因
        orderAmount: 0, // 退款金额
        orderSn: '', // ?号
        typeActual: [
          {
            typeActual: 3,
            typeActualStr: '仅退款'
          },
          {
            typeActual: 1,
            typeActualStr: '退货退款'
          }
        ], // 可选的退款类型 如 退货退款、仅退款

      }, // 退款页面详情
      array: [],
      reason: [],
      reasonAll: [], // 接受所有的退款原因
      typeName: '', // 监听退款类型
      index: null,
      indexWhy: null,
      orderAmount: '20.05', // 订单金额
      description: '', // 退款说明
      totalNum: 0,
      images: [],
      scrollTop: 0,
      repairSn: '', // 逆向单号（repairSn）网单号（cOrderSn）网单id(orderProductId)
      repairSnOne: '', // 首次申请退款
      invoiceInvaild: false, // 是否显示专票提示弹窗
      otherFefund: null
    }
  },
  methods: {
    /**
     * @description  退货类型选择
    */
    bindPickerChange (picker, value, index) {
      // this.index = index
      event.stopPropagation();
    },
    onConfirmType (value, index) {
      this.typeName = value
      this.ifChange = false
      this.showType = false
      this.index = index
      event.stopPropagation();
    },
    onCancelType () {
      this.showType = false
      event.stopPropagation();
    },
    /**
     * @description  退款原因选择
    */
    bindPickerChangeWhy: function(picker, value, index) {
      this.indexWhy = index
      event.stopPropagation();
    },
    onConfirmWhy (index) {
      this.ifWhy = false
      this.showWhy = false
      this.indexWhy = index
      event.stopPropagation();
    },
    onCancelWhy () {
      this.showWhy = false
      event.stopPropagation();
    },
    /**
     * @description  提交退款申请
    */
    submission() {
      this.$toast.loading({
        forbidClick: true,
        message: '加载中...',
        duration: 0
      })
      if (this.pos) {
        // 提交前必填项验证
        if (this.userName === '') {
          this.$toast('请输入姓名!');
          return false
        } else if (this.userName.length < 2 || this.userName > 20) {
          this.$toast('请输入正确的姓名!');
          return false
        }
        if (this.bankCard === '') {
          this.$toast('请输入银行卡卡号!');
          return false
        } else if (!(/^\d{14,24}/.test(this.bankCard))) {
          this.$toast('请输入正确的银行卡号!');
          return false
        }
        if (this.userId === '') {
          this.$toast('请输入身份证号！');
          return false
        } else if (!(/^\d{15}$/.test(this.userId) || /^\d{17}([A-z]|\d)$/.test(this.userId))) {
          this.$toast('身份证号格式有误，请重新填写！');
          return false
        }
        if (this.userPhone === '') {
          this.$toast('请输入预留手机号!');
          return false
        } else if (!(/^1\d{10}$/.test(this.userPhone))) {
          this.$toast('请输入正确的手机号!');
          return false
        }
      }
      // 退款类型，退款原因必填验证
      if (this.reasonIndex === null) {
        this.$toast('请选择退款类型!');
        return false
      }
      if (this.indexWhy === null) {
        this.$toast('请选择退款原因!');
        return false
      }
      // picUrl图片路径处理
      var context = this
      // 有图片上传
      if (context.images.length > 0) {
        this.loading = true
        personalApi.uploadPictureBybase64({
          base64s: context.images.map(img => img.content.split(',')[1])
        }).then(imgRes => {
          if (imgRes.data.success) {
            if (!(context.pos) && context.repairSn.repairSn !== undefined) {
              const data = {
                repairSn: context.repairSn.repairSn, // 逆向号
                reason: context.reason[context.indexWhy], // 退款原因
                description: context.description, // 退款说明
                typeActual: context.reasonIndex, // 退款类型
                picUrl: imgRes.data.data.fileList.map(img => img.imgUrl).join() // "图片url 最多6张 英文逗号分隔"
              }
              // 修改申请退款接口
              personalApi.modifyOrderRepair(data).then(res => {
                if (res.data.success) {
                  context.$toast.clear()
                  context.$router.replace({ path: '/details', query: { orderProductId: context.repairSn.orderProductId } }) // 退货详情回传网单id
                  if (context.firstReturn) {} else {
                    context.$router.go(-1)
                  }
                } else {
                  context.$toast(res.data.message)
                }
              })
            } else if (context.pos) {
              // 银行卡信息校验
              const bankData = {
                bankCard: context.bankCard, // 银行卡号
                idCard: context.userId, // 身份证号
                name: context.userName, // 开户姓名
                phone: context.userPhone, // 开户手机
              }
              personalApi.bankCardValidate(bankData).then(res => {
                if (res.data.success) {
                // 提交逆向申请
                  const data1 = {
                    cOrderSn: context.repairSnOne, // 网单号
                    typeActual: context.reasonIndex, // 退款类型
                    reason: context.reason[context.indexWhy], // 退款原因
                    description: context.description, // 描述
                    picUrl: imgRes.data.data.fileList.map(img => img.imgUrl).join(), // 图片
                    bankCard: context.bankCard, // 银行卡号
                    bankCode: res.data.data.bankCode, // 银行编号？
                    bankName: res.data.data.bankName, // 银行名称？
                    idCard: context.userId, // 身份证号
                    name: context.userName, // 开户姓名
                    phone: context.userPhone, // 开户手机
                  }
                  // 必填项验证
                  if (data1.typeActual === null) {
                    context.$toast.fail('请选择退款类型')
                    return
                  }
                  if (data1.reason === undefined) {
                    context.$toast.fail('请选择退款原因')
                    return
                  }
                  // if (data1.description === '') {
                  //   context.$toast.fail('请填写退款说明')
                  //   return
                  // }
                  personalApi.requestOrderRepair(data1).then(res => {
                    if (res.data.success) {
                      Toast.success({
                        message: '提交成功',
                        duration: 2000,
                        onClose: () => {
                          // 订单列表跳转
                          context.$router.go(-1)
                        }
                      })
                      // context.$router.replace({ path: '/details', query: { orderProductId: context.orderProductId } }) // 退货详情回传网单id
                      // if (context.firstReturn) {} else {
                      //   context.$router.go(-1)
                      // }
                    } else {
                      context.$toast(res.data.message)
                    }
                  })
                } else {
                  context.$toast(res.data.message)
                }
              })
            } else if (context.repairSnOne !== '' && !context.pos) { // 首次退款pos为false
              // 提交逆向申请
              const data2 = {
                cOrderSn: context.repairSnOne, // 网单号
                typeActual: context.reasonIndex, // 退款类型
                reason: context.reason[context.indexWhy], // 退款原因
                description: context.description, // 描述
                picUrl: imgRes.data.data.fileList.map(img => img.imgUrl).join(), // 图片
              }
              personalApi.requestOrderRepair(data2).then(res => {
                if (res.data.success) {
                  Toast.success({
                    message: '提交成功',
                    duration: 2000,
                    onClose: () => {
                    // 订单列表跳转
                      context.$router.go(-1)
                    }
                  })
                  // context.$router.replace({ path: '/details', query: { orderProductId: context.orderProductId } }) // 退货详情回传网单id
                  // if (context.firstReturn) {} else {
                  //   context.$router.go(-1)
                  // }
                } else {
                  context.$toast(res.data.message)
                }
              })
            }
          } else {
            this.loading = false
            context.$toast.fail(imgRes.data.message)
          }
        }).catch(() => {
          this.loading = false
          context.$toast.fail('上传图片失败')
        })
      } else {
        // 判断是否是修改申请提交
        if (!(context.pos) && context.repairSn.repairSn !== undefined) {
          const data = {
            repairSn: context.repairSn.repairSn, // 逆向号
            reason: context.reason[context.indexWhy], // 退款原因
            description: context.description, // 退款说明
            typeActual: context.reasonIndex, // 退款类型
          }
          // 修改申请退款接口
          personalApi.modifyOrderRepair(data).then(res => {
            if (res.data.success) {
              this.$toast.clear()
              context.$router.replace({ path: '/details', query: { orderProductId: context.repairSn.orderProductId } }) // 退货详情回传网单id
              if (context.firstReturn) {} else {
                context.$router.go(-1)
              }
            } else {
              context.$toast(res.data.message)
            }
          })
        } else if (context.repairSnOne !== '' && context.pos) {
          // 银行卡信息校验
          const bankData = {
            bankCard: context.bankCard, // 银行卡号
            idCard: context.userId, // 身份证号
            name: context.userName, // 开户姓名
            phone: context.userPhone, // 开户手机
          }
          personalApi.bankCardValidate(bankData).then(res => {
            if (res.data.success) {
              // 提交逆向申请
              const data1 = {
                cOrderSn: context.repairSnOne, // 网单号
                typeActual: context.reasonIndex, // 退款类型
                reason: context.reason[context.indexWhy], // 退款原因
                description: context.description, // 描述
                bankCard: context.bankCard, // 银行卡号
                bankCode: res.data.data.bankCode, // 银行编号？
                bankName: res.data.data.bankName, // 银行名称？
                idCard: context.userId, // 身份证号
                name: context.userName, // 开户姓名
                phone: context.userPhone, // 开户手机
              }
              // 必填项验证
              if (data1.typeActual === null) {
                context.$toast.fail('请选择退款类型')
                return
              }
              if (data1.reason === undefined) {
                context.$toast.fail('请选择退款原因')
                return
              }
              // if (data1.description === '') {
              //   context.$toast.fail('请填写退款说明')
              //   return
              // }
              personalApi.requestOrderRepair(data1).then(res => {
                if (res.data.success) {
                  Toast.success({
                    message: '提交成功',
                    duration: 2000,
                    onClose: () => {
                      // 订单列表跳转
                      context.$router.go(-1)
                    }
                  })
                  // context.$router.replace({ path: '/details', query: { orderProductId: context.orderProductId } }) // 退货详情回传网单id
                  // if (context.firstReturn) {} else {
                  //   context.$router.go(-1)
                  // }
                } else {
                  context.$toast(res.data.message)
                }
              })
            } else {
              context.$toast(res.data.message)
            }
          })
        } else if (context.repairSnOne !== '' && !context.pos) { // 首次退款pos为false
          // 提交逆向申请
          const data2 = {
            cOrderSn: context.repairSnOne, // 网单号
            typeActual: context.reasonIndex, // 退款类型
            reason: context.reason[context.indexWhy], // 退款原因
            description: context.description, // 描述
            picUrl: '',
          }
          personalApi.requestOrderRepair(data2).then(res => {
            if (res.data.success) {
              Toast.success({
                message: '提交成功',
                duration: 2000,
                onClose: () => {
                  // 订单列表跳转
                  context.$router.go(-1)
                }
              })

              // context.$router.replace({ path: '/details', query: { orderProductId: context.orderProductId } }) // 退货详情回传网单id
              // if (context.firstReturn) {} else {
              //   context.$router.go(-1)
              // }
            } else {
              context.$toast(res.data.message)
            }
          })
        }
      }
    },
    /**
     * @description  退款类型选择弹窗
    */
    showPopupType() {
      this.showType = true
    },
    /**
     * @description  退款原因选择弹窗
    */
    showPopupWhy() {
      this.showWhy = true
    },
    /**
     * @description  姓名校验
    */
    checkName() {
      if (this.userName === '') {
        this.$toast('请输入姓名!');
      } else if (this.userName.length < 2 || this.userName > 20) {
        this.$toast('请输入正确的姓名!');
      }
    },
    /**
     * @description  银行卡校验
    */
    checkCard() {
      if (this.bankCard === '') {
        this.$toast('请输入银行卡卡号!');
      } else if (!(/^\d{14,24}/.test(this.bankCard))) {
        this.$toast('请输入正确的银行卡号!');
      }
    },
    /**
     * @description  身份证校验
    */
    checkId() {
      if (this.userId === '') {
        this.$toast('请输入身份证号！');
      } else if (!(/^\d{15}$/.test(this.userId) || /^\d{17}([A-z]|\d)$/.test(this.userId))) {
        this.$toast('身份证号格式有误，请重新填写！');
      }
    },
    /**
     * @description  手机号校验
    */
    checkPhone() {
      if (this.userPhone === '') {
        this.$toast('请输入预留手机号!');
      } else if (!(/^1\d{10}$/.test(this.userPhone))) {
        this.$toast('请输入正确的手机号!');
      }
    },
    /**
     * @description  title返回
    */
    onClickLeft() {
      this.$router.back()
    },
    /**
     * @description  支持银行跳转
    */
    onClickRight() {
      this.$router.push({ path: '/bankslist' })
    },
    /**
     * @description  传本地完成后
    */
    // uploadImg(file) {
    //   this.images.forEach(item => {
    //     console.log('前', item.file)
    //     if (/\/(?:jpeg|png)/i.test(item.file.type) && item.file.size > 1000000) {
