    //         let file = new window.File([blob], item.file.name, { type: item.file.type })
    //         item.file = file
    //         console.log('item.file', item.file)
    //       }
    //     }
    //   })
    // },
    /**
     * @description  将base64转换为blob
    */
    // dataURLtoBlob: function(dataurl) {
    //   var arr = dataurl.split(',');
