    //     u8arr[n] = bstr.charCodeAt(n);
    //   }
    //   return new Blob([u8arr], { type: mime });
    // },
    /**
     * 价格保留两位小数
     */
    toFixedTwo(number) {
      const price = Number(number).toFixed(2);
      return price
    },
